@use "media-breakpoints" as *;

$primary-color: #ffcc00;
$background-color: #fff;
$text-color: #000;
$secondary-text: #bbb;
$button-bg: #6f42c1;
$button-hover: #5936a8;

.brand-section{
    background-color: $background-color;
    color: $text-color;
    padding: 18vh 0vh 5vh 0vh;
    position: relative; 
    display: flex;
    align-items: center; /* Ensures vertical centering */
     
    @keyframes slides {
      from {
        transform: translateX(0);
      }
      to {
        transform: translateX(-100%);
      }
    }

.logos {
  overflow: hidden;
  padding: 30px 0px;
  white-space: nowrap;
  position: relative;

  &::before {
    position: absolute;
    top: 0;
    content: '';
    width: 250px;
    height: 100%;
    z-index: 2;
    left: 0;
    background: linear-gradient(to left, #ffffff00, rgb(255, 255, 255));
    @include media-breakpoint-down(md) {
      background: linear-gradient(to left, #ffffff00, rgba(255, 255, 255, 0));
    }
  }

   &::after {
    position: absolute;
    top: 0;
    content: '';
    width: 250px;
    height: 100%;
    z-index: 2;
    right: 0;
    background: linear-gradient(to right, #ffffff00, rgb(255, 255, 255));
     @include media-breakpoint-down(md) {
      background: linear-gradient(to left, #ffffff00, rgba(255, 255, 255, 0));
    }
  }

  .logo_items {
    display: inline-block;
    animation: 35s slides infinite linear;

    &:hover {
      animation-play-state: paused;
    }

    img{
      height: 50px;
      padding: 0 30px;
    }
  }

}


}