@use "media-breakpoints" as *;

$primary-color: #ffcc00;
$background-color: #fff;
$text-color: #000;
$secondary-text: #bbb;
$button-bg: #6f42c1;
$button-hover: #5936a8;

.about-me-section{
    background-color: $background-color;
    color: $text-color;
    padding: 0px 0;
    position: relative; 
    display: flex;
    align-items: center; /* Ensures vertical centering */

    h3{
        font-size:65px;
        padding:0px 30px;
        margin-bottom:70px; 
        max-width: 90%; 
        font-family: 'Italiana', serif;  
        @include media-breakpoint-down(md) {
            font-size:48px;
            padding:0px 0px;
             margin-bottom:0px; 
        }
    } 
    
    h3 div {
        display:inline-block;
    }

    #text-anim{
            display:block;
            @include media-breakpoint-down(md) {
                display:none;
            }
    }
    #text-anim-mobile{
        display:none;
        @include media-breakpoint-down(md) {
            display:block;
        }
    }

    .left-section {
        min-height: 500px; // Fixed height option
        height: 100%; // Full height of parent container
         background: radial-gradient(circle,#ff00f2 -160%,#fff 60%);
        // Alternatively, use viewport height
        height: 100vh; 
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        
        // Flexbox approach for vertical centering
        display: flex;
        align-items: center;
        justify-content: center;
        
        // Responsive approach
        @media (max-width: 992px) {
            min-height: 300px; // Adjust for smaller screens
            height: auto;
        }

        @include media-breakpoint-down(md) {
            min-height: 275px; 
        }
    }

    .about-me-content{
        @include media-breakpoint-down(md) {
            padding:0px 20px;
        }
    }
    
    .col-lg-6 {
        display: flex;
        align-items: center; /* Vertically centers the content */
        height: 100%; /* Ensure it takes full height */
        
        
        img{
            padding:30px 0px 0px 0px;
        }

        p{
            font-family: 'Poppins', serif;
        }
    }

}