@use "media-breakpoints" as *;

$text-color: #ffcc00;
$background-color: #fff;
$text-color: #000;
$secondary-text: #bbb;
$button-bg: #6f42c1;
$button-hover: #5936a8;

.case-study-archive-section {
  padding: 10vh 0vh !important;
  background-color: $background-color;
  color: $text-color;
  padding: 0;
  position: relative;
  display: flex; 
  align-items: center; // Ensures vertical centering\
 
   .container {
    margin: 0 auto;
    padding: 0 20px;

    .row {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        gap: 20px;

         .col-lg-6 {
            flex: 1;
            min-width: 300px;

            h4 {
                font-size: 24px;
                font-weight: bold;
                background: linear-gradient(90deg, #005eff 0, #ff00f2 100%);
                -webkit-background-clip: text;
                background-clip: text;
                color: transparent;
                display: inline-block;
                 font-family: 'Italiana', serif;  
                font-weight: 700px;
                font-size: 52px;
            }

            p { 
                padding:20px 0px;
                color: #000;
                font-size: 16px;
                line-height: 1.5;
            }

            img {
                width: 100%;
                max-width: 500px;
                display: block;
                margin: 0 auto;
            }
        }
    }
  }

  img{
    width:100%;
  }
}

.case-study-list-section{
 margin-bottom: 12vh;

    h2{
        font-size: 18px;
        font-weight: bold;
        background: linear-gradient(90deg, #005eff 0, #ff00f2 100%);
        -webkit-background-clip: text;
        background-clip: text;
        color: transparent;
        display: inline-block;
          font-family: 'Italiana', serif;  
        font-weight: 700px;
        font-size: 52px;
        padding:8vh 0vh 4vh 0vh;
    }

    .card-wrapper{ 
        
        .card{
            background-color: #ffffff;
            box-shadow: 2px 5px 10px 0 rgba(0, 0, 0, .2);
            border-radius: 30px;
               .logo{
                padding: 35px;
                border-radius: 30px;
                min-height:180px;
                max-height:180px;
            }
            .card-body{
                border-radius: 30px;
                h5{
                font-size:20px; 
                font-weight:bold;
                    @include media-breakpoint-down(md) {
                        font-size:18px; 
                        font-weight:bold;
                    }
                }
                .btn-wrapper{
                    text-align:right;
                    .arrow{
                    width: 16%;
                    }
                }
                
            }
        }
            
    }
}

