@use "media-breakpoints" as *;

$primary-color: #ffcc00;
$background-color: #fff;
$text-color: #000;
$secondary-text: #bbb;
$button-bg: #6f42c1;
$button-hover: #5936a8;
 
.hero-section {
    background-color: $background-color;
    color: $text-color;
    padding: 80px 0;
    
    text-align: center;
    overflow-x: hidden; // Fix horizontal scrollbar issue

    @include media-breakpoint-down(md) {
        padding: 130px 0;
        position: relative;
    }

    .container {
        max-width: 1200px;
        margin: 0 auto;
    }

    .row {
        align-items: center;
        justify-content: center;
    }

    .bg-fade {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 1;
        width: 100%;
        max-width: 100%;
    }

    .web-line {
        position: absolute;
        top: 0;
        left: 72%;
        transform: translateX(-50%);
        z-index: 2;
        width: auto;
        max-width: 100%;
    }

    .hero-image {
        position: relative;
        max-width: 100%;
        border-radius: 10px;
        filter: brightness(90%);
        z-index: 3;
        -webkit-mask-image: linear-gradient(to bottom, #000 70%, rgba(0, 0, 0, 0) 100%);
        display: block;
        margin: 0 auto; // Center image
    }

    .digital-marketer {
        position: relative;
        bottom: 32%;
        left: 50%;
        transform: translateX(-50%);
        z-index: 4;
        background: linear-gradient(90deg, #005EFF 0%, #FF00F2 100%);
        -webkit-background-clip: text;
        background-clip: text;
        color: transparent;
        font-family: 'Poppins', sans-serif;
        font-weight: 700;
        text-align: center;

        h2 {
            font-size: 50px;
            text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
            line-height: 1.2;
        }
    }

    .left-text,
    .right-text {
        font-size: 16px;
        font-weight: 600;
        text-align: center;
    }

    .left-text {
        position: relative;
        margin-bottom: 20px;
        top: 7%;
    }

    .right-text {
        position: relative;
        margin-top: 20px;
    }

    .right-icon {
        display: flex;
        justify-content: center;
        margin-bottom: 20px;

        .web-icon {
            max-width: 80px;
        }
    }

    // Responsive Fixes
    @include media-breakpoint-down(lg) {
        padding: 50px 0;

        .row {
            flex-direction: column;
            align-items: center;
            text-align: center;
        }

        .hero-image {
            max-width: 90%;
            height: auto;
        }

        .left-text,
        .right-text {
            position: static;
            text-align: center;
            padding: 10px;
        }

        .digital-marketer h2 {
            font-size: 40px;
        }
    }

    @include media-breakpoint-down(md) {
        .hero-image {
            max-width: 80%;
        }

        .digital-marketer h2 {
            font-size: 36px;
        }

        .left-text {
            margin-top: 100px;
        }

        .right-text {
            margin-top: 40px;
        }
    }

    @include media-breakpoint-down(sm) {
        padding: 12vh 0vh 0vh 0vh;

        .hero-image {
            max-width: 90%;
        }

        .digital-marketer h2 {
            font-size: 32px;
        }

        .left-text {
            font-size: 14px;
            margin-top: 20px;
        }

        .right-text {
            font-size: 14px;
            margin-top: 20px;
        }

        .right-icon .web-icon {
            max-width: 60px;
        }
    }
}
 

