@use "media-breakpoints" as *;

$text-color: #000;
$background-color: #fff;
$secondary-text: #bbb;
$button-bg: #6f42c1;
$button-hover: #5936a8;
$primary-color: #e41c23;
$border-radius: 12px;
$box-shadow: 0 6px 20px rgba(0, 0, 0, 0.08);
$enhanced-box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);
$transition: all 0.3s ease;

.single-case-study-section {
    padding: 17vh 0 !important;
    background-color: $background-color;
    color: $text-color;
    position: relative;

    @include media-breakpoint-down(md) {
        padding: 15vh 3vh !important;
    }
    
    .container {
        max-width: 1280px;
 
        .section-header {
            margin-bottom: 30px;
            
            .left-section {
                padding-right: 15px;
                @include media-breakpoint-down(md) {
                    padding-right: 0px;
                    padding-left: 0px;
                }
                
                @include media-breakpoint-down(lg) {
                    padding-right: 0;
                    margin-bottom: 25px;
                }
                
                .header-text {
                    text-align: left;
                    margin-bottom: 25px;
                    
                    h2 {
                        font-size: 2.5rem;
                        font-weight: 800;
                        margin-bottom: 15px;
                        line-height: 1.2;
                        letter-spacing: -0.5px;

                        @include media-breakpoint-down(md) {
                            font-size: 1.8rem;
                        }
                        
                        span {
                            color: $primary-color;
                            font-weight: 800;
                            position: relative;
                            text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
                            
                            &::after {
                                content: '';
                                position: absolute;
                                width: 100%;
                                height: 4px;
                                background-color: rgba($primary-color, 0.2);
                                bottom: 2px;
                                left: 0;
                                z-index: -1;
                            }
                        }
                    }
                    
                    p {
                        font-size: 1.1rem;
                        line-height: 1.6;
                        margin-bottom: 20px;
                        color: rgba($text-color, 0.8);
                    }
                }
                
                .contributors {
                    text-align: left;
                    margin-bottom: 25px;
                    font-size: 0.9rem;
                    
                    a {
                        color: #0077b5;
                        text-decoration: none;
                        margin: 0 2px;
                        display: inline-flex;
                        align-items: center;
                        transition: $transition;
                        
                        &:hover {
                            color: #005885; 
                            transform: translateY(-1px);
                        }
                        
                        svg {
                            margin-left: 4px;
                        }
                    }
                }
                
                .featured-image-1 {
                    margin-top: 20px;
                    position: relative;
                    overflow: hidden;
                    
                    img {
                        width: 100%;
                        border-radius: $border-radius;
                        object-fit: cover;
                        box-shadow: $enhanced-box-shadow;
                        transition: $transition;
                        
                        &:hover {
                            transform: translateY(-5px);
                            box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
                        }
                    }
                }
            }
            
            .right-section {
                padding-left: 15px;
                
                @include media-breakpoint-down(lg) {
                    padding: 0 !important;
                }
                
                .featured-image-2 {
                    height: 100%;
                    display: flex;
                    align-items: flex-start;
                    position: relative;
                    overflow: hidden;
                    
                    img {
                        width: 100%;
                        border-radius: $border-radius;
                        object-fit: cover;
                        box-shadow: $enhanced-box-shadow;
                        transition: $transition;
                        
                        &:hover {
                            transform: translateY(-5px);
                            box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
                        }
                    }
                }
            }
        }

        .section-footer {
            margin-top: 30px;
            
            // Set a fixed height for the footer row to ensure all images have the same height
            .left-section, .center-section, .right-section {
                height: 180px; // Fixed height for all sections
                
                @include media-breakpoint-down(md) {
                    height: auto;
                    margin-bottom: 20px;
                }
            }
            
            .left-section { 
                padding-right: 15px;
                
                 @include media-breakpoint-down(lg) {
                    padding: 0 !important;
                }
                
                .featured-image-3 {
                    height: 100%;
                    position: relative;
                    overflow: hidden;
                    
                    img {
                        width: 100%;
                        height: 100%; // Full height of the container
                        border-radius: $border-radius;
                        object-fit: cover;
                        box-shadow: $enhanced-box-shadow;
                        transition: $transition;
                        
                        &:hover {
                            transform: translateY(-5px);
                            box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
                        }
                    }
                }
            }
            
            .center-section { 
                padding-left: 10px;
                padding-right: 10px;
                
                @include media-breakpoint-down(md) {
                    padding-left: 0;
                    padding-right: 0;
                }

                
                .featured-image-4 {
                    height: 100%;
                    position: relative;
                    overflow: hidden;
                    
                    img {
                        width: 100%;
                        height: 100%; // Full height of the container
                        border-radius: $border-radius;
                        object-fit: cover;
                        box-shadow: $enhanced-box-shadow;
                        transition: $transition;
                        
                        &:hover {
                            transform: translateY(-5px);
                            box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
                        }
                    }
                }
            }
            
            .right-section {
                padding-left: 15px;
                
                @include media-breakpoint-down(md) {
                    padding-left: 0;
                }
                
                .featured-image-5 {
                    height: 100%;
                    position: relative;
                    overflow: hidden;
                    
                    img {
                        width: 100%;
                        height: 100%; // Full height of the container
                        border-radius: $border-radius;
                        object-fit: cover;
                        box-shadow: $enhanced-box-shadow;
                        transition: $transition;
                        
                        &:hover {
                            transform: translateY(-5px);
                            box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
                        }
                    }
                }
            }
        }
        
        // Video Button Styles for all featured images
        .featured-image-1,
        .featured-image-2,
        .featured-image-3,
        .featured-image-4,
        .featured-image-5 {
            position: relative;
            
            .video-btn {
                position: absolute;
                bottom: 15px;
                right: 15px;
                background-color: $primary-color;
                color: white;
                border: none;
                border-radius: 50px;
                padding: 8px 16px;
                font-size: 0.9rem;
                font-weight: 600;
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 6px;
                cursor: pointer;
                opacity: 0.9;
                transition: $transition;
                z-index: 2;
                box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
                
                svg {
                    width: 16px;
                    height: 16px;
                }
                
                &:hover {
                    opacity: 1;
                    transform: translateY(-2px);
                    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
                }
            }
        }
    }
    
    // Video Modal Styles
    .video-modal {
        display: none;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.85);
        z-index: 1000;
        justify-content: center;
        align-items: center;
        
        &.show {
            display: flex;
        }
        
        .video-modal-content {
            position: relative;
            width: 80%;
            max-width: 900px;
            background-color: #000;
            border-radius: $border-radius;
            box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
            
            .close-modal {
                position: absolute;
                top: -40px;
                right: 0;
                color: white;
                font-size: 2rem;
                cursor: pointer;
                z-index: 1001;
                transition: $transition;
                
                &:hover {
                    transform: scale(1.1);
                    color: $primary-color;
                }
            }
            
            .video-container {
                position: relative;
                padding-bottom: 56.25%; // 16:9 aspect ratio
                height: 0;
                overflow: hidden;
                
                iframe {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    border-radius: $border-radius;
                }
            }
        }
    }
}