@use "media-breakpoints" as *;

$text-color: #ffcc00;
$background-color: #fff;
$text-color: #000;
$secondary-text: #bbb;
$button-bg: #6f42c1;
$button-hover: #5936a8;

.case-studies-section {
  background-color: $background-color;
  color: $text-color;
  padding: 0;
  position: relative;
  display: flex; 
  align-items: center; // Ensures vertical centering

  h3{ 
    background: linear-gradient(90deg, #005EFF 0%, #FF00F2 100%);
        -webkit-background-clip: text;
        background-clip: text;
        color: transparent;
        display: inline-block;
        font-family: 'Poppins', serif;
        font-weight:700px;
        font-size:52px;
  }

  p{
    font-family: 'Poppins', serif;
  }
  

main {
  position: relative;
  width: calc(min(90rem, 90%));
  margin: 0 auto;
  min-height: 100vh;
  column-gap: 3rem;
  padding-block: min(20vh, 3rem);
}

.bg, .bg2 {
  position: fixed;
  z-index: -1;
  opacity: 0;
}

.bg {
  top: -4rem;
  left: -12rem;
}

.bg2 {
  bottom: -2rem;
  right: -3rem;
  width: 9.375rem;
}

main > div {
  span {
    text-transform: uppercase;
    letter-spacing: 1.5px;
    font-size: 1rem;
    color: $text-color;
  }

  h1 {
    text-transform: capitalize;
    letter-spacing: 0.8px;
    font-family: "Roboto", sans-serif;
    font-weight: 900;
    font-size: clamp(3.4375rem, 3.25rem + 0.75vw, 4rem);
    background: linear-gradient(45deg, $text-color, #000000);
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  hr {
    display: block;
    background: $text-color;
    height: 0.25rem;
    width: 6.25rem;
    border: none;
    margin: 1.125rem 0 1.875rem 0;
  }

  p {
    line-height: 1.6;
  }

  > a {
    display: inline-block;
    text-decoration: none;
    text-transform: uppercase;
    color: $text-color;
    font-weight: 500;
    background: $text-color;
    border: 2px solid #c2c2c2;
    border-radius: 3.125rem;
    margin-top: 2.188rem;
    padding: 0.625rem 1.875rem;
    transition: 0.3s ease-in-out;

    &:hover {
      border: 0.125rem solid $text-color;
      color: $text-color;
    }
  }
}

.swiper {
  width: 100%;
  padding-top: 3.125rem;

  &-pagination {
    bottom: 1.25rem !important;

    &-bullet,
    &-bullet-active {
      background: $text-color;
    }
  }
}

.swiper-slide {
  width: 18.75rem;
  height: 28.125rem;
  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: flex-start;
  border-radius: 25px;

  h2 {
    color: #fff;
    font-family: "Roboto", sans-serif;
    font-weight:600;
    font-size: 1.4rem;
    line-height: 1.4;
    margin-bottom: 0.625rem;
    padding-left: 1.563rem;
  }

  p {
    color: #dadada;
    font-family: "Roboto", sans-serif;
    font-weight: 300;
    padding: 0 1.563rem;
    line-height: 1.6;
    font-size: 0.75rem;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }

  a {
    margin: 1.25rem 1.563rem 3.438rem 1.563rem;
    padding: 0.438em 1.875rem;
    font-size: 0.9rem;
    background-color:#fff;
    color:#000;
    text-decoration:none;
    border-radius:15px;

    &:hover {
      color: $text-color;
    }
  }

  div {
    display: none;
    opacity: 0;
    padding-bottom: 4.625rem;
  }

  &-active div {
    display: block;
    opacity: 1;
    background: #211c1cb8;
    padding-top: 22px;
    border-bottom-right-radius: 25px;
    border-bottom-left-radius: 25px;
  }
}

@mixin bg-gradient($url) {
  background: linear-gradient(to top, #0f2027, #203a4300, #2c536400), url($url) no-repeat 50% 50% / cover;
}


.swiper-slide--one { @include bg-gradient("../assets/img/case-1.png"); }
.swiper-slide--two { @include bg-gradient("../assets/img/case-3.png"); }
.swiper-slide--three { @include bg-gradient("../assets/img/case-3.png"); }
.swiper-slide--four { @include bg-gradient("https://images.unsplash.com/photo-1515309025403-4b0184873cef?ixlib=rb-4.0.3&auto=format&fit=crop&w=735&q=80"); }
.swiper-slide--five { @include bg-gradient("https://images.unsplash.com/photo-1628944682084-831f35256163?ixlib=rb-4.0.3&auto=format&fit=crop&w=687&q=80"); }


.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right {
  background-image: none;
}

.swiper-button-prev,
.swiper-button-next {
  color: #fff !important;
  bottom: 50%; // Move it to the bottom
  top: auto; // Reset top positioning
  z-index: 10;
}

.swiper-button-prev { 
  left: 5%;
}

.swiper-button-next { 
  right: 5%;
}


@media screen and (min-width: 48rem) {
  main {
    display: flex;
    align-items: center;
  }
  .bg, .bg2 {
    opacity: 0.1;
  }
}

@media screen and (min-width: 93.75rem) {
  .swiper {
    width: 85%;
  }
}
  
}